html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 58.55em;
  margin: 0;
  padding: 0;
  background-image: linear-gradient(var(--soft-violet), var(--soft-blue));
}

:root {
  --very-dark-des-blue: hsl(238, 29%, 16%);
  --soft-red: hsl(14, 88%, 65%);
  --soft-violet: hsl(273, 75%, 66%);
  --soft-blue: hsl(240, 73%, 65%);
  --very-dark-grayish-blue: hsl(237, 12%, 33%);
  --dark-grayish-blue: hsl(240, 6%, 50%);
  --light-grayish-blue: hsl(240, 5%, 91%);
}

:root {
  --font-kumbh: "Kumbh Sans", sans-serif;
}

h1,
h2,
h3,
h4,
p {
  font-family: var(--font-kumbh);
  margin: 0;
}

.hero {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.hero__container {
  width: 60em;
  min-height: 30em;
  display: flex;
  flex-direction: row;
  background-color: white;
  border-radius: 25px;
  padding: 4rem 5rem;
  overflow: hidden;
  transition: all 1s ease-in-out;
}
.hero__container__pict {
  flex: 1;
}
.hero__container__pict .container {
  position: relative;
}
.hero__container__pict .container__illustration {
  position: absolute;
  top: 0;
  left: -10em;
  z-index: 1;
}
.hero__container__pict .container__pattern {
  position: absolute;
  top: -16em;
  left: -31em;
  width: 200%;
  z-index: 1;
}
.hero__container__faq {
  flex: 1;
}
.hero__container__faq .title {
  color: var(--very-dark-des-blue);
  font-size: 2rem;
  margin-bottom: 2rem;
}
.hero__container__faq .card__question {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--light-grayish-blue);
  margin-bottom: 1.5rem;
}
.hero__container__faq .card__question__title {
  font-size: 1rem;
  font-weight: 400;
  transition: all 0.2s ease-in-out;
}
.hero__container__faq .card__question__title:hover {
  color: var(--soft-red);
}
.hero__container__faq .card__question .titleCheck {
  font-weight: 700;
}
.hero__container__faq .card__answear {
  margin: 1rem 0;
  display: none;
}
.hero__container__faq .card__answear p {
  color: var(--very-dark-grayish-blue);
  line-height: 1.6;
  font-size: 0.9rem;
}
.hero__container__faq .card .check {
  display: block;
}